import React, { useEffect, forwardRef } from 'react';
import './style.scss';

import initAnimations from '../../scripts/animate';
import BlueprintLogo from "../../Assets/LogoBlueprint.png";

const SobreMim = forwardRef((props, ref) => {
    useEffect(() => {
      initAnimations(); 
    }, []);
    
    return (
        <div className="SobreMim" ref={ref}>
            <img className='animate' src={BlueprintLogo} alt="Blueprint representation of the logo."/>

            <div className="SobreMim-Content">
                <h1 className="title"> Sobre Mim </h1>

                <p> Desenvolvedor Front-end, com ampla experincia em React. Apaixonado por design e criao de layouts e animaes para websites e desenvolvimento de identidade visual, unindo criatividade e tcnica no desenvolvimento de solues digitais integradas.</p>
            </div>
        </div>
    );
});

export default SobreMim;